import React from 'react';
import Nav from './Nav';
import { Redirect,Link } from 'react-router-dom';
import axios from 'axios';
import Footer from './Footer';
// import update from '../Assets/Css/update.css';

class Updateprofile extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        let rData = JSON.parse(token)

        let loggedIn = true
        if(token == null){
            loggedIn = false
        }
        this.state = {
            rData,
            id:rData[2],
            loggedIn,
            data:'',

        }
    }

    componentDidMount() {
        // console.log(this.state.id)
        axios.post("http://localhost:4000/login/shopDetail",{id:this.state.id})
            .then(response => {
                // console.log(response.data.result[0])
                this.setState({data : response.data.result[0]})
                
            })
            .catch(err => {
                console.log(err);
            })
        
    }

    render(){
        if(this.state.loggedIn === false){
            return <Redirect to="/" />
        }
        const{ data } = this.state
        return(
            
            <div className="hi">
                <Nav /><br /><br /><br />
                <div className="container" style={{paddingTop: '5%'}}>
                  <div className="row">
                      <div className="col-lg-3 col-md-3 hidden-sm"></div>
                        <div className="col-lg-6 col-md-6 hidden-sm">
                            <div align="center">
                            <   div className="card details" style={{paddingTop: '2%'}}>
                            {/* {
                                data.map(res => ( */}
                                    <div className="card-body table-responsive"> 
                                        <img src={'http://localhost:4000/images/'+data.image} alt={data.image} height="200px" width="200px" /><br /><br />
                                        <table>
                                            <tbody>
                                                <tr>
                                                    <td><h5><b><em>Shop Name</em></b></h5></td> : 
                                                    <td><h5>{data.shopname}</h5></td>
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>OwnerName</em></b></h5></td> :
                                                    <td><h5>{data.owner}</h5></td>
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>PhoneNumber</em></b></h5></td> :
                                                    <td><h5>{data.phone}</h5></td>
                                                    
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>ShopLocation </em></b></h5></td> :
                                                    <td><h5>{data.location}</h5></td>
                                                    
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>Description</em></b></h5></td> :  
                                                    <td><h5>{data.description}</h5></td>
                                                    
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>Email</em></b></h5></td> :
                                                    <td><h5>{data.email}</h5></td>
                                                    
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>Category</em></b></h5></td> :
                                                    <td><h6>{data.cat1}</h6></td>,
                                                    <td><h6>{data.cat2}</h6></td>,
                                                    <td><h6>{data.cat3}</h6></td>.
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>DeliveryBoysDetails</em></b></h5></td> : <td><h6>{data.delivery1}</h6></td>,<td><h6>{data.delivery2}</h6></td>,<td><h6>{data.delivery3}</h6></td>.
   
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div><br />
                                    <h5><em>Update the Delivery Boys data if U are not Updated*</em></h5><br />
                                    <div align="center">
                                        <Link className="btn btn-danger btn-lg" to="/updateDelivery">Update</Link>
                                    </div>
                                </div>
                                {/* ))
                            } */}
                            </div>
                        </div>
                      <div className="col-lg-2 col-md-2 hidden-sm"></div>
                  </div>
              </div>
               <div>
                   <Footer />
               </div>
            </div>
        )
    }
}

export default Updateprofile;