import React,{Component} from 'react';
import Nav from './Nav';
import axios from 'axios';
import {Redirect} from 'react-router-dom';
import loading from '../Assets/Images/spinner-loader-animation.gif';
// import nodatafound from '../Assets/Images/nodatafound.png';
import Footer from './Footer';

class S_order extends Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        let Data = JSON.parse(token)
        // console.log(JSON.parse(token))
        // console.log(Data)
        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            Data1:Data[0],
            shop:Data[1],
            loggedIn,
            Details: [],
            // currentPage: 1,
            // DetailsperPage: 1,
           loading: false,
           dataStatus:true,

        }
        
    }

   

    componentDidMount(){
        axios.post('http://localhost:4000/customer/s_order',{shop:this.state.shop})
        .then(res => {
            // console.log(res.data);
            if(res.data.status === 1){
                this.setState({
                    dataStatus: false,
                    Details: res.data.result, 
                })
            }else{
                alert("no data found");
            }
        })
    }

    approoval=(e) =>{
        e.preventDefault();
        this.setState({ loading: true })
        axios.post("http://localhost:4000/customer/approoval",{id:+e.target.value})
            .then(response => {
                // console.log(response)
                if(response.data.status === 1){
                    window.location.reload(true)
                }else{
                    this.props.history.push('/')
                }
                
            })
            .catch(err => {
                console.log(err)
            })
    }
    cancel=(e) =>{
        e.preventDefault();
        this.setState({ loading: true })
        axios.post("http://localhost:4000/customer/cancel",{id:+e.target.value})
            .then(response => {
                // console.log(response)
                if(response.data.status === 1){
                    window.location.reload(true)
                }else{
                    this.props.history.push('/')
                }
                
            })
            .catch(err => {
                console.log(err)
            })
    }
    complete=(e) =>{
        e.preventDefault();
        this.setState({ loading: true })
        axios.post("http://localhost:4000/customer/complete",{id:+e.target.value})
            .then(response => {
                // console.log(response)
                if(response.data.status === 1){
                    window.location.reload(true)
                }else{
                    this.props.history.push('/')
                }
                
            })
            .catch(err => {
                console.log(err)
            })
            
    }

    render(){
        // if(this.state.edit){
        //     return <Redirect to="/edit" />
        // }
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
        // const { Data1,update } = this.state
        // console.log(Data1);
        const{ Details,loading }=this.state;

        return(
            <div className="home">
                <Nav /><br /><br /><br />
                <div className="container-fluid">
                    {/* <Welcome /><br /> */}
                    <div className="row">
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                        {/* {dataStatus ? <Loading /> : */}
                        <div className="col-lg-10 col-md-10 hidden-sm">
                        {loading ? <Loading /> :
                            <div className="card">
                                <div className="card-body">
                                    <div className="table-responsive">
                                        <table className="table  table-hover" border="2px">
                                            <thead className="thead-dark">
                                                <div>
                                                <tr>
                                                    <th>#</th>
                                                    <th>Image</th>
                                                    {/* <th>Customer id</th>
                                                    <th>Item id</th> */}
                                                    <th>Item Colour</th>
                                                    <th>Item Size</th>
                                                    <th>Price</th>
                                                    <th>Customer name</th>
                                                    <th>Customer email</th>
                                                    <th>Customer phonenumber</th>
                                                    <th>H-no</th>
                                                    <th>Area</th>
                                                    <th>City</th>
                                                    <th>Payment GateWay</th>
                                                    <th>Delivery Boy</th>
                                                    <th>Approval</th>
                                                    <th>Cancel Order</th>
                                                    <th>Order Complete</th>
                                                </tr>
                                                </div>
                                            </thead>
                                            <tbody className="tbody-light">
                                    
                                            { Details.map((data) => (
                                                    <tr key={data}>
                                                        {`${data.status}`==='0' ? <div>
                                                        <td>{data.order_id}</td>
                                                        <td><a target="_blank" href={'http://localhost:4000/images/'+data.item_image}><img src={'http://localhost:4000/images/'+data.item_image} alt={data.image1} height="70px" width="70px" /></a></td>
                                                        {/* <td>{data.customer_id}</td>
                                                        <td>{data.item_id}</td> */}
                                                        <td>{data.item_colour}</td>
                                                        <td>{data.item_size}</td>
                                                        <td>{data.item_price}</td>
                                                        <td>{data.c_name}</td>
                                                        <td>{data.c_email}</td>
                                                        <td>{data.c_phone}</td>
                                                        <td>{data.hno}</td>
                                                        <td>{data.area}</td>
                                                        <td>{data.city}</td>
                                                        <td>{data.payment}</td>
                                                        <td>{data.delivery_boy}</td>
                                                        <td><button type="button" class="btn btn-success" value={data.order_id}  onClick={this.approoval} >Aprooval</button></td> 
                                                        <td><button type="button" class="btn btn-danger" value={data.order_id}  onClick={this.cancel} >Cancel Order</button></td>   
                                                        <td>Wait for approoval</td>
                                                        </div>:null}
                                                        {`${data.status}`==='1' ? <div>
                                                        <td>{data.order_id}</td>
                                                        <td><a target="_blank" href={'http://localhost:4000/images/'+data.item_image}><img src={'http://localhost:4000/images/'+data.item_image} alt={data.image1} height="70px" width="70px" /></a></td>
                                                        {/* <td>{data.customer_id}</td>
                                                        <td>{data.item_id}</td> */}
                                                        <td>{data.item_colour}</td>
                                                        <td>{data.item_size}</td>
                                                        <td>{data.item_price}</td>
                                                        <td>{data.c_name}</td>
                                                        <td>{data.c_email}</td>
                                                        <td>{data.c_phone}</td>
                                                        <td>{data.hno}</td>
                                                        <td>{data.area}</td>
                                                        <td>{data.city}</td>
                                                        <td>{data.payment}</td>
                                                        <td>{data.delivery_boy}</td>
                                                        <td>Aprooved</td> 
                                                        <td>Product is shipped</td>  
                                                        <td><button type="button" class="btn btn-info" value={data.order_id}  onClick={this.complete} >Completed</button></td>   
                                                        </div>:null}
                                                        {`${data.status}`==='2' ? <div>
                                                        <td>{data.order_id}</td>
                                                        <td><a target="_blank" href={'http://localhost:4000/images/'+data.item_image}><img src={'http://localhost:4000/images/'+data.item_image} alt={data.image1} height="70px" width="70px" /></a></td>
                                                        {/* <td>{data.customer_id}</td>
                                                        <td>{data.item_id}</td> */}
                                                        <td>{data.item_colour}</td>
                                                        <td>{data.item_size}</td>
                                                        <td>{data.item_price}</td>
                                                        <td>{data.c_name}</td>
                                                        <td>{data.c_email}</td>
                                                        <td>{data.c_phone}</td>
                                                        <td>{data.hno}</td>
                                                        <td>{data.area}</td>
                                                        <td>{data.city}</td>
                                                        <td>{data.payment}</td>
                                                        <td>{data.delivery_boy}</td>
                                                        <td>product cancelled</td> 
                                                        <td>product cancelled</td>   
                                                        <td>product cancelled</td>   
                                                        </div>:null}
                                                        {/* {`${data.status}`==='3' &&dataStatus ? 
                                                            <div className="alert alert-success">
                                                                successfully delivered product
                                                            </div>
                                                        :null} */}
                                                    </tr>
                                            ))}  
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        }
                        </div>
                    {/* } */}
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                    </div>
                </div>
                <div>
                   <Footer />
                </div>
            </div>
        )
        }
    }
}


class Loading extends Component{
    render(){
        return(
            <div className="loading">
                <center><img src={loading} alt="loading" width="400px" height="400px"/></center>
            </div>
        )
    }
}

