import React from 'react';
import axios from 'axios';
import {Redirect,Link } from 'react-router-dom';
import loading from '../Assets/Images/spinner-loader-animation.gif';
import MainNav from './MainNav';
import Footer from './Footer';
// import login from '../Assets/Css/login.css';

class Login extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")

        let loggedIn = true
        if(token == null){
            loggedIn = false
        }
        
        this.state={
            email:'',
            pwd:'',
            // status: '',
            // Details:null,
            loggedIn,
            dataState: false,
            dataStatus:false,
        }
        
    }
    changeHandler=(e) =>{
        this.setState({[e.target.name]: e.target.value})
    }

    submitHandler=(e)=>{
        e.preventDefault();
        this.setState({dataStatus: true})
        axios.post("http://localhost:4000/login/customer",this.state)
        .then(response => {
            // console.log(response)
            if(response.data.status1 === 1){
                // return <Redirect to="/home" />
                // this.props.history.push('/home');
                // console.log(response.data.data[0].status);
                // console.log("Details",JSON.stringify([response.data.data[0].status,response.data.data[0].shopname,response.data.data[0].id]))
                // this.setState({
                //     Details : [response.data.data[0].status,response.data.data[0].shopname,response.data.data[0].id],
                //     // Data1 : JSON.stringify(Data)
                // })
                localStorage.setItem("token",JSON.stringify([response.data.data[0].id,response.data.data[0].updateStatus]));
                this.setState({
                    loggedIn: true,
                    // status : response.data.data[0].status
                })
            }else{
                this.setState({
                    dataState : true,
                    email:'',
                    pwd:''
                })
            }
            setInterval(() => {
                this.setState({dataState : false,
                    dataStatus : false,
                })
            },100000)
        })
    }

    render(){
        if(this.state.loggedIn){
            return <Redirect to="/cdashboard" />
        }
        const{ email,pwd,dataState,dataStatus } = this.state
        return(
            <>
              <MainNav /><br /><br /><br />
            <div className="container login">
                <div className="row">
                    <div className="col-lg-4 col-md-4 hidden-sm"></div>
                    <div className="col-lg-4 col-md-4 hidden-sm">
                        <div className="card">
                        { dataState ? <div className="alert alert-danger">Invalid Credentials</div> : null }
                            <center><h4 style={{ color: 'red'}}><b>Customer Login</b></h4></center><hr />
                            {dataStatus ? <Loading /> :
                            <div className="card-body">
                                <form onSubmit={this.submitHandler}>
                                    <input type="email" placeholder="Enter your Email" name="email" value={email} className="form-control" onChange={this.changeHandler} /><br />
                                    <input type="password" placeholder="Enter your password" name="pwd" value={pwd} className="form-control" onChange={this.changeHandler} /><br />
                                    <button type="submit" className="btn btn-danger">Submit</button><br />
                                     
                                </form>
                                <Link to="/f_pwdPage">Forgot Password</Link>
                            </div>
                            }
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-4 hidden-sm"></div>
                </div>
                
            </div>
            <div>
                   <Footer />
               </div>
            </>
        )
    }

}

class Loading extends React.Component{
    render(){
        return(
            <div className="loading">
                <center><img src={loading} alt="loading" width="400px" height="400px"/></center>
            </div>
        )
    }
}

export default Login;