import React from 'react';
import axios from 'axios';
import Nav from './Nav';
import { Redirect} from 'react-router-dom';
import Footer from './Footer';

class Edit extends React.Component{
    constructor(props){
        super(props)

        const token = localStorage.getItem("token")
        let rData = JSON.parse(token)
        // console.log(rData[0]);
        let loggedIn = true
        if(token == null){
            loggedIn = false
        }
        
        this.state={
            rData,
            loggedIn,
            pid: props.match.params.id,
            shopname: rData[1],
            

            //step 
            id:'',
            shop:'',
            cat:'',
            itemtype:'',
            item:'',
            size:'',
            size2:'',
            size3:'',
            size4:'',
            size5:'',
            size6:'',
            years:'',
            colour:'',
            colour2:'',
            colour3:'',
            colour4:'',
            brand:'',
            desc:'',
            avaliable:'',
            dprice:'',
            price:'',
            
            dataState: false
        }
    }

    componentDidMount(){
        axios.post('http://localhost:4000/login/getdata',{shop:this.state.shopname,id:this.state.pid})
        .then(res => {
            // console.log(res);
            if(res.status === 200){
            this.setState({
                id: res.data[0].id,
                shop:res.data[0].shopname,
                size:res.data[0].size,
                size2:res.data[0].size2,
                size3:res.data[0].size3,
                size4:res.data[0].size4,
                size5:res.data[0].size5,
                size6:res.data[0].size6,
                years:res.data[0].suited_yr,
                colour:res.data[0].colour,
                colour2:res.data[0].colour2,
                colour3:res.data[0].colour3,
                colour4:res.data[0].colour4,
                brand:res.data[0].brand,
                desc:res.data[0].description,
                avaliable:res.data[0].avaliable,
                dprice:res.data[0].dprice,
                price:res.data[0].price,
            })
        }else{
            alert("something went wrong");
        }
        })
    }

    handleChange = (e) => {
        this.setState({[e.target.name]: e.target.value});
    }

    submitHandler=(e) =>{
        e.preventDefault();
        axios.post("http://localhost:4000/login/edit",this.state)
        .then(response => {
            if(response.status === 200){
              this.setState({
                 dataState: true
              })
            }else{
              alert("Not submitted..!");
            }
        })
        .catch(err => {
            console.log(err)
        })
        setInterval(() => {
            this.setState({dataState : false,
            })
        },10000)
    }
    render(){
        if(this.state.loggedIn === false){
            return <Redirect to="/" />
        }
        const { shopname,id,size,size2,size3,size4,size5,size6,years,colour,colour2,colour3,colour4,brand,desc,avaliable,dprice,price,dataState } = this.state;
        return(
            <>
                <Nav /><br /><br /><br /><br />
                <div className="container">
                <div className="row">
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                    <div className="col-lg-6 col-md-6 hidden-sm">
                        <div className="card">
                            { dataState ? <div className="alert alert-success">Successfully submited</div> : null}
                            <div align="center">
                                <h3><b>Edit Product</b></h3><hr />
                            </div>
                            <div className="card-body">
                                <form onSubmit={this.submitHandler}>
                                    <h3><b>Plzz select first 3 category for Edit*</b></h3>
                                <input type="hidden" className="form-control" placeholder="Enter the Size" name="id" value={id} onChange={this.handleChange} /><br />
                                <input type="hidden" className="form-control" placeholder="Enter the Size" name="shopname" value={shopname} onChange={this.handleChange} /><br />
                                    <h6><b>Category<span className="required-star">*</span></b></h6>
                                        <select className="form-control" name="cat" onChange={this.handleChange} required="TRUE">
                                            <option>--Select the Category--</option>
                                            <option value="BoysWear">BoysWear</option>
                                            <option value="GirlsWear">GirlsWear</option>
                                            <option value="LadiesWear">LadiesWear</option>
                                            <option value="MensWear">MensWear</option>
                                        </select><br />
                                    <h6><b>Item Type<span className="required-star">*</span></b></h6>
                                        <select className="form-control" name="itemtype" onChange={this.handleChange} required="TRUE">
                                            <option>--Select the Category--</option>
                                            <option value="combined">Combined-Item</option>
                                            <option value="single">Single-Item</option>
                                        </select><br />
                                    <p>if you selected combined item so below is optional and not select the below one</p>
                                    <h6><b>Item<span className="required-star">*</span></b></h6>
                                        <select className="form-control" name="item" onChange={this.handleChange} required="TRUE">
                                            <option>--Select the Category--</option>
                                            <option value="combined">Combined-Item</option>
                                            <option value="shirt">Shirt</option>
                                            <option value="pant/jeans">pant/jeans</option>
                                            <option value="t-shirt">T-shirt</option>
                                        </select><br />
                                        <h6><b>Size<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size" value={size} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6>If u had multiple size in same dress plzz Enter with Age the dress Suits</h6>
                                    <h6><b>Size2<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size2" value={size2} onChange={this.handleChange} /><br />
                                    <h6><b>Size3<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size3" value={size3} onChange={this.handleChange} /><br />
                                    <h6><b>Size4<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size4" value={size4} onChange={this.handleChange} /><br />
                                    <h6><b>Size5<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size5" value={size5} onChange={this.handleChange} /><br />
                                    <h6><b>Size6<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size6" value={size6} onChange={this.handleChange} /><br />
                                    <h6><b>Age of dress suits<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="years" value={years} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>colours<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="colour" value={colour} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6>If u had same model dress with different Colours so plzz Enter..</h6>
                                    <h6><b>colours2<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="colour2" value={colour2} onChange={this.handleChange} /><br />
                                    <h6><b>colours3<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="colour3" value={colour3} onChange={this.handleChange} /><br />
                                    <h6><b>colours4<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="colour4" value={colour4} onChange={this.handleChange} /><br />
                                    <h6><b>Brand<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="brand" value={brand} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Description of dress<span className="required-star">*</span></b></h6>
                                        <textarea type="msg" className="form-control" placeholder="Enter the Size" name="desc" value={desc} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>No of availability dress<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="avaliable" value={avaliable} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Actual Price<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="dprice" value={dprice} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Price<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="price" value={price} onChange={this.handleChange} required="TRUE"/><br />
                                    <div className="col text-right">
                                        <button type="submit" className=" btn btn-danger btn-lg">
                                            Edit 
                                        </button>
                                    </div>
                                    
                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                </div>
            </div>
            <div>
                <Footer />
            </div>
            </>
        )
    }
}

export default Edit;