import React from 'react';
import CNav from './CNav';
import Footer from './Footer';
import { Redirect,Link } from 'react-router-dom';
import axios from 'axios';

class Shops extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        // let Data = JSON.parse(token)

        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            loggedIn,
            Data:[]
        }
    }

    componentDidMount(){
        axios.get('http://localhost:4000/login/AproovedDetails')
        .then(res => {
            // console.log(res);
            if(res.status === 200){
            this.setState({
                // dataStatus: false,
                Data: res.data, 
            })
        }else{
            this.setState({
                // dataStatus:false,
            })
            alert("No data Found");
        }
        })
    }

    render(){
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
            const { Data }=this.state;
        return(
            <div>
               <CNav /><br /><br /><br />
               <div className="container" style={{paddingTop: '5rem'}}>
                   <div className="row justify-content-center">
                        <div className="col-md-2 hidden-sm"></div>
                       {/* <div className="col-lg-2 col-md-2 hidden-sm"></div> */}
                       {/* <div className="col-lg-8 col-md-8 hidden-sm"> */}
                           {
                               Data.map((shops) => (
                                   <div className="col-md-4 col-12">
                                        <div className="card text-black bg-white border-0" key={shops.id} style={{width: '20rem',height: '36rem'}}>
                                            <div className="Image" style={{padding: '3%'}}>
                                                <img src={'http://localhost:4000/images/'+shops.image} className="card-image-top" alt={shops.image} height="300px" width="100%" />
                                            </div>
                                            <div className="card-body text-dark">
                                                <center><h2 className="card-title" style={{color: 'red'}}><b><em>{ shops.shopname }</em></b></h2></center>
                                                <p className="card-text text-justify" style={{fontSize:'22px'}}>{ shops.location}</p>
                                                <p className="card-text text-justify" style={{fontSize:'22px'}}>{ shops.cat1},{ shops.cat2},{ shops.cat3}</p>
                                                <div align="center">
                                                    <Link type="button" to={"/items/"+shops.shopname} className="btn btn-dark btn-lg">View Shop</Link>
                                                </div>
                                            </div>
                                        </div><br />
                                    </div>
                               ))
                           }
                       {/* </div> */}
                       {/* <div className="col-lg-2 col-md-2 hidden-sm"></div> */}
                   </div>
               </div>
               <div>
                   <Footer />
               </div>
            </div>
        )
    }
}
}

export default Shops;