import React from 'react';
import axios from 'axios';
import MainNav from './MainNav';
import Footer from './Footer';
// import loading from '../Assets/Images/126.gif';
// import { Redirect } from 'react-router-dom';
// import login from '../Assets/Css/login.css';

class Login extends React.Component{
    constructor(props){
        super(props)
        this.state={

            step: 1,
            insert_id: null,

            //step 1
            sname:'',
            name:'',
            phone:'',
            location:'',
            desc:'',

            //step2
            email:'',
            pwd:'',
            con_pwd:'',

            //step3
            cat1:'',
            cat2:'',
            cat3:'',

            //step 4
            file: null,
            filename:null,
            dataState: false,
            
        }
    }

    nextStep = () => {
        const { step } = this.state;

        // this.setState({
        //     // insert_id: response.data.insertId,
        //     step: step + 1
        // })
  
        if(step === 1){
            
            axios.post("http://localhost:4000/login/step1",this.state)
            .then(response => {
                if(response.status === 200){
                  this.setState({
                      insert_id: response.data.insertId,
                      step: step + 1
                  })
                }else{
                  alert("Not submitted..!");
                }
            })
            .catch(err => {
                console.log(err)
            })
            
        }
        if(step === 2){
            
            axios.post("http://localhost:4000/login/step2",this.state)
            .then(response => {
                if(response.status === 200){
                  this.setState({
                      step: step + 1
                  })
                }else{
                  alert("Not submitted..!");
                }
            })
            .catch(err => {
                console.log(err)
            })
        }
        
        if(step === 3){
            
            axios.post("http://localhost:4000/login/step3",this.state)
            .then(response => {
                if(response.status === 200){
                  this.setState({
                      step: step + 1
                  })
                }else{
                  alert("Not submitted..!");
                }
            })
            .catch(err => {
                console.log(err)
            })
        }
      }
  
      prevStep = () => {
        const { step } = this.state;
        this.setState({
            step: step - 1
        });
      }
      
      handleChange = input =>e => {
        this.setState({[input]: e.target.value});
      }
  
      onFileChange=(e) =>{
        //   console.log([e.target.files[0].name]);
        this.setState({ filename: e.target.files})
        this.setState({ file: [e.target.files[0].name] })
      }
  
      submitHandler = (e) =>{
        e.preventDefault();
       
        axios.post("http://localhost:4000/login/filemv",this.state)
            .then(response => {
                const formData  = new FormData() 
                for (const key of Object.keys(this.state.filename)) {
                    formData.append('filename', this.state.filename[key])
                }
                axios.post("http://localhost:4000/login/fileupload",formData)
                    .then(res => {
                        // console.log(res)
                        if(res.data.status === 1){
                            // this.props.history.push('/products');
                            window.location.reload(true);
                            this.setState({
                                dataState : true
                            })
                           
                        }else{
                            return <div>Something went wrong</div>
                        }
                        setInterval(() => {
                            this.setState({dataState : false})
                        },10000)
                    })
                    .catch(err => {
                        console.log(err)
                    });
        
        })
      }
  
      showStep = () => {
        const { step,insert_id,sname,name,phone,location,desc,email,pwd,con_pwd,cat1,cat2,cat3,dataState,file } = this.state;
  
        if(step === 1)
            return (<Admin1 
                nextStep = {this.nextStep} 
                handleChange = {this.handleChange} 
                sname={sname}
                name={name} 
                phone={phone}
                location={location}
                desc={desc}
                dataState={dataState}
                
            />);
        if(step === 2)
            return (<Admin2
                nextStep = {this.nextStep} 
                prevStep = {this.prevStep}
                handleChange = {this.handleChange} 
                insert_id = {insert_id}
                email={email} 
                pwd={pwd}
                con_pwd={con_pwd}
               
            />);
        if(step === 3)
            return (<Admin3 
                nextStep = {this.nextStep}
                prevStep = {this.prevStep}
                handleChange = {this.handleChange}
                insert_id = {insert_id}
                cat1={cat1} 
                cat2={cat2}
                cat3={cat3} 
                
            />);
        if(step === 4)
            return (<Admin4 
                onFileChange = {this.onFileChange}
                insert_id = {insert_id} 
                submitHandler = {this.submitHandler}
                file={file}
                filename={file}
                prevStep = {this.prevStep}
            />);
      }
      render(){
        const { step,dataState } = this.state;
        
            return (
                <div className="addItems">
                    <MainNav /><br /><br /><br />
                    <div className="container">
                        <h2>Step {step} of 4.</h2>
                        { dataState ? <div className="alert alert-success">successfully added</div> : null }
                        {this.showStep()}
                    </div>
                    <div>
                        <Footer />
                    </div>
                    
                </div>
            );
        
    }
}


class Admin1 extends React.Component{
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    }

    render(){
        const{ sname,name,phone,location,desc,handleChange } = this.props
        return(
            <div className="container">
                <div className="row">
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                    <div className="col-lg-6 col-md-6 hidden-sm">
                        <div className="card" style={{paddingTop: '2%'}}>
                            <div align="center">
                                <h3><b>Register</b></h3><hr />
                            </div>
                            <div className="card-body">
                                <form>
                                    <h6><b>Shopname<span className="required-star">*</span></b></h6>
                                        <input type="text" placeholder="Enter your shopname" className="form-control" name="sname" value={sname} onChange={handleChange('sname')}  required="TRUE"/><br />
                                    <h6><b>Owner name<span className="required-star">*</span></b></h6>
                                        <input type="text" placeholder="Enter your ownername" className="form-control" name="name" value={name} onChange={handleChange('name')} required="TRUE"/><br />
                                    <h6><b>Mobile<span className="required-star">*</span></b></h6>
                                        <input type="number" placeholder="Enter your phone" className="form-control" name="phone" value={phone} onChange={handleChange('phone')} required="TRUE"/><br />
                                    <h6><b>Location<span className="required-star">*</span></b></h6>
                                        <input type="text" placeholder="Enter your location and address" className="form-control" name="location" value={location} onChange={handleChange('location')} required="TRUE"/><br />
                                    <h6><b>Description<span className="required-star">*</span></b></h6>
                                        <textarea type="text" placeholder="Enter your description" className="form-control" name="desc" value={desc} onChange={handleChange('desc')} required="TRUE"/><br />
                                    
                                    
                                    <div className="col text-right">
                                        <button className=" btn btn-danger btn-lg" onClick={this.continue}>
                                            Next 
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                </div>
            </div>
        )
    }
}

class Admin2 extends React.Component {
    
  continue = e => {
      e.preventDefault();
      this.props.nextStep();
      
  }

  back = e => {
      e.preventDefault();
      this.props.prevStep();
  }
 
  render() {
      const { email, pwd, con_pwd,handleChange} = this.props;
      return (
              <div className="container">
                  {this.props.insert_id}
                  <div className="row">
                  <div className="col-lg-3 col-md-3 hidden-sm"></div>
                        <div className="col-lg-6 col-md-6 hidden-sm">
                            <div className="card" style={{paddingTop: '2%'}}>
                                <div className="card-body"> 
                                    <h6><b>Email<span className="required-star">*</span></b></h6>
                                        <input type="email" placeholder="Enter your email" className="form-control" name="email" value={email} onChange={handleChange('email')} required="TRUE"/><br />
                                    <h6><b>Password<span className="required-star">*</span></b></h6>
                                        <input type="password" placeholder="Enter your password" className="form-control" name="pwd" value={pwd} onChange={handleChange('pwd')} required="TRUE"/><br />
                                    <h6><b>Confirm Password<span className="required-star">*</span></b></h6>
                                        <input type="password" placeholder="Enter your confirm password" className="form-control" name="con_pwd" onChange={handleChange('con_pwd')} value={con_pwd} required="TRUE"/><br />
                                    <div className="col text-left">
                                        <button className="btn btn-danger btn-lg" onClick={this.back}>
                                             Back
                                        </button>
                                    </div>
                                    <div className="col text-right">
                                        <button className="btn btn-success btn-lg" onClick={this.continue}>
                                            Next 
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                      <div className="col-lg-3 col-md-3 hidden-sm"></div>
                  </div>
              </div>
      );
  }
}


class Admin3 extends React.Component {
  continue = e => {
      e.preventDefault();
      this.props.nextStep();
  }
  back = e => {
      e.preventDefault();
      this.props.prevStep();
  }
  render(){
      const {  handleChange } = this.props;
      return(
          <>
           <div className="container">
              {this.props.insert_id}
                  <div className="row">
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                        <div className="col-lg-6 col-md-6 hidden-sm">
                            <div className="card" style={{paddingTop: '2%'}}>
                                <div className="card-body"> 
                                    <h6><b>Category 1</b></h6>
                                        <select className="form-control" name="cat1" onChange={handleChange('cat1')}>
                                            <option>--Select the Category--</option>
                                            <option value="Boys Wear" name="cat1" onChange={handleChange('cat1')}>Boys Wear</option>
                                        </select><br />
                                    <h6><b>Category 2</b></h6>
                                        <select className="form-control" name="cat2" onChange={handleChange('cat2')}>
                                            <option>--Select the Category--</option>
                                            <option value="lades/girls Wear" name="cat1" onChange={handleChange('cat1')}>ladies Wear/Girls Wear</option>
                                        </select><br />
                                    <h6><b>Category 3</b></h6>
                                        <select className="form-control" name="cat3" onChange={handleChange('cat3')}>
                                            <option>--Select the Category--</option>
                                            <option value="Mens Wear" >Mens Wear</option>
                                        </select><br />
                                    <div className="col text-left">
                                        <button className="btn btn-danger btn-lg" onClick={this.back}>
                                             Back
                                        </button>
                                    </div>
                                    <div className="col text-right">
                                        <button className="btn btn-success btn-lg" onClick={this.continue}>
                                            Next 
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    <div className="col-lg-2 col-md-2 hidden-sm"></div>
                  </div>
              </div>
          </>
      );
  }
}


class Admin4 extends React.Component {
  back = e => {
      e.preventDefault();
      this.props.prevStep();
  }

  submit = e =>{
      e.preventDefault();
      this.props.submitHandler(e);
  }
  
  render(){
      // const {  } = this.props;
      return(
          <>
           <div className="container">
              {this.props.insert_id}
                  <div className="row">
                      <div className="col-lg-3 col-md-3 hidden-sm"></div>
                        <div className="col-lg-6 col-md-6 hidden-sm">
                            <div className="card" style={{paddingTop: '2%'}}>
                                <div className="card-body"> 
                                    <form onSubmit={this.submit} encType="multipart/form-data">
                                        <input type="file"  name="file" class="form-control"  onChange={this.props.onFileChange} multiple/><br />
                                        <div className="col text-left">
                                            <button className="btn btn-danger btn-lg" onClick={this.back}>
                                                 Back
                                            </button>
                                        </div>
                                        <div className="col text-right">
                                            <input type="submit" className=" btn btn-success btn-lg"  value="Submit "/>
                                            {/* <button type="submit" className="btn btn-success" >
                                                Submit 
                                            </button> */}
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                      <div className="col-lg-2 col-md-2 hidden-sm"></div>
                  </div>
              </div>
          </>
      );
  }
}

