import React from 'react';
import axios from 'axios';
import Nav from './Nav';
import { Redirect} from 'react-router-dom';
import Footer from './Footer';
// import loading from '../Assets/Images/126.gif';
// import login from '../Assets/Css/login.css';

class Addproduct extends React.Component{
    constructor(props){
        super(props)

        const token = localStorage.getItem("token")
        let rData = JSON.parse(token)
        // console.log(rData[0]);
        let loggedIn = true
        if(token == null){
            loggedIn = false
        }
        
        this.state={
            rData,
            loggedIn,
            step: 1,
            insert_id: null,

            //step 1
            id:rData[2],
            shopname: rData[1],
            cat:'',
            itemtype:'',
            item:'',
            size:'',
            size2:'',
            size3:'',
            size4:'',
            size5:'',
            size6:'',
            years:'',
            colour:'',
            colour2:'',
            colour3:'',
            colour4:'',
            brand:'',
            desc:'',
            avaliable:'',
            dprice:'',
            price:'',

            //step2
            file: null,
            filename:null,
            
            dataState: false
        }
    }

    nextStep = () => {
        const { step } = this.state;

        // this.setState({
        //     // insert_id: response.data.insertId,
        //     step: step + 1
        // })
  
        if(step === 1){
            // this.setState({ loading: true })
            axios.post("http://localhost:4000/login/add1",this.state)
            .then(response => {
                if(response.status === 200){
                  this.setState({
                      insert_id: response.data.insertId,
                      step: step + 1
                  })
                }else{
                  alert("Not submitted..!");
                }
            })
            .catch(err => {
                console.log(err)
            })
            
            
        }
       
      }

      prevStep = () => {
        const { step } = this.state;
        this.setState({
            step: step - 1
        });
      }
      
      handleChange = input =>e => {
        this.setState({[input]: e.target.value});
      }
  
      onFileChange=(e) =>{
        // console.log("files",Array.from(e.target.files));
        // console.log("files",[e.target.files[0].name,e.target.files[1].name,e.target.files[2].name])
        this.setState({ filename: e.target.files})
        this.setState({ file: [e.target.files[0].name,e.target.files[1].name,e.target.files[2].name] })
        
    }
  
      submitHandler = (e) =>{
        e.preventDefault();
        this.setState({ loading: true })
        axios.post("http://localhost:4000/login/addfile",this.state)
            .then(response => {
                const formData  = new FormData() 
                for (const key of Object.keys(this.state.filename)) {
                    formData.append('filename', this.state.filename[key])
                }
                axios.post("http://localhost:4000/login/addfileupload",formData)
                    .then(res => {
                        // console.log(res)
                        if(res.data.status === 1){
                            // this.props.history.push('/products');
                            window.location.reload(true);
                            this.setState({
                                dataState : true
                            })
                           
                        }else{
                            return <div>Something went wrong</div>
                        }
                        setInterval(() => {
                            this.setState({dataState : false})
                        },10000)
                    })
                    .catch(err => {
                        console.log(err)
                    });
        
        })
      }
  
      showStep = () => {
        const { step,insert_id,shopname,id,cat,itemtype,item,size,size2,size3,size4,size5,size6,years,colour,colour2,colour3,colour4,brand,desc,avaliable,dprice,price,dataState,file } = this.state;
  
        if(step === 1)
            return (<Add1 
                nextStep = {this.nextStep} 
                handleChange = {this.handleChange}
                id={id}
                shopname={shopname} 
                cat={cat}
                itemtype={itemtype} 
                item={item}
                size={size}
                size2={size2}
                size3={size3}
                size4={size4}
                size5={size5}
                size6={size6}
                years={years}
                colour={colour}
                colour2={colour2}
                colour3={colour3}
                colour4={colour4}
                brand={brand}
                avaliable={avaliable}
                dprice={dprice}
                price={price}
                desc={desc}
                dataState={dataState}
                
            />);
            if(step === 2)
            return (<Add2
                onFileChange = {this.onFileChange}
                insert_id = {insert_id} 
                submitHandler = {this.submitHandler}
                file={file}
                filename={file}
                prevStep = {this.prevStep}
            />);
      }

      render(){
        if(this.state.loggedIn === false){
            return <Redirect to="/" />
        }
        const { step,dataState } = this.state;
        
            return (
                <div className="addItems">
                    <Nav /><br /><br /><br />
                    
                    <div className="container">
                        <h2>Step {step} of 2.</h2>
                        { dataState ? <div className="alert alert-success">successfully added</div> : null }
                        {this.showStep()}
                    </div>
                    <div>
                       <Footer />
                   </div>
                    
                </div>
            );
        
    }
}

class Add1 extends React.Component{
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    }

    render(){
        const{ id,shopname,size,size2,size3,size4,size5,size6,years,colour,colour2,colour3,colour4,brand,desc,avaliable,dprice,price,handleChange } = this.props
        return(
            <>
            <div className="container">
                <div className="row">
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                    <div className="col-lg-6 col-md-6 hidden-sm">
                        <div className="card">
                            <div align="center">
                                <h3><b>Add Product</b></h3><hr />
                            </div>
                            <div className="card-body">
                                <form>
                                <input type="hidden" className="form-control" placeholder="Enter the Size" name="id" value={id} onChange={handleChange('id')} /><br />
                                <input type="hidden" className="form-control" placeholder="Enter the Size" name="shopname" value={shopname} onChange={handleChange('shopname')} /><br />
                                    <h6><b>Category<span className="required-star">*</span></b></h6>
                                        <select className="form-control" name="cat" onChange={handleChange('cat')} required="TRUE">
                                            <option>--Select the Category--</option>
                                            <option value="BoysWear">BoysWear</option>
                                            <option value="GirlsWear">GirlsWear</option>
                                            <option value="LadiesWear">LadiesWear</option>
                                            <option value="MensWear">MensWear</option>
                                        </select><br />
                                    <h6><b>Item Type<span className="required-star">*</span></b></h6>
                                        <select className="form-control" name="itemtype" onChange={handleChange('itemtype')} required="TRUE">
                                            <option>--Select the Category--</option>
                                            <option value="combined">Combined-Item</option>
                                            <option value="single">Single-Item</option>
                                        </select><br />
                                    <p>if you selected combined item so below is optional and not select the below one</p>
                                    <h6><b>Item<span className="required-star">*</span></b></h6>
                                        <select className="form-control" name="item" onChange={handleChange('item')} required="TRUE">
                                            <option>--Select the Category--</option>
                                            <option value="combined">Combined-Item</option>
                                            <option value="shirt">Shirt</option>
                                            <option value="pant/jeans">pant/jeans</option>
                                            <option value="t-shirt">T-shirt</option>
                                        </select><br />
                                        <h6><b>Enter all Sizes with year the dress Suits</b></h6>
                                    <h6><b>Size<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size" value={size} onChange={handleChange('size')} required="TRUE"/><br />
                                    <h6>If u had multiple size in same dress plzz Enter</h6>
                                    <h6><b>Size2<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size2" value={size2} onChange={handleChange('size2')} /><br />
                                    <h6><b>Size3<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size3" value={size3} onChange={handleChange('size3')} /><br />
                                    <h6><b>Size4<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size4" value={size4} onChange={handleChange('size4')} /><br />
                                    <h6><b>Size5<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size5" value={size5} onChange={handleChange('size5')} /><br />
                                    <h6><b>Size6<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="size6" value={size6} onChange={handleChange('size6')} /><br />
                                    <h6><b>Age of dress suits<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="years" value={years} onChange={handleChange('years')} required="TRUE"/><br />
                                    <h6><b>colours<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="colour" value={colour} onChange={handleChange('colour')} required="TRUE"/><br />
                                    <h6>If u had same model dress with different Colours so plzz Enter..</h6>
                                    <h6><b>colours2<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="colour2" value={colour2} onChange={handleChange('colour2')} /><br />
                                    <h6><b>colours3<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="colour3" value={colour3} onChange={handleChange('colour3')} /><br />
                                    <h6><b>colours4<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="colour4" value={colour4} onChange={handleChange('colour4')} /><br />
                                    <h6><b>Brand<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="brand" value={brand} onChange={handleChange('brand')} /><br />
                                    <h6><b>Description of dress<span className="required-star">*</span></b></h6>
                                        <textarea type="msg" className="form-control" placeholder="Enter the Size" name="desc" value={desc} onChange={handleChange('desc')} /><br />
                                    <h6><b>No of availability dress<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="avaliable" value={avaliable} onChange={handleChange('avaliable')} required="TRUE"/><br />
                                    <h6><b>Actual Price<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="dprice" value={dprice} onChange={handleChange('dprice')} required="TRUE"/><br />
                                    <h6><b>Price<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Size" name="price" value={price} onChange={handleChange('price')} required="TRUE"/><br />
                                    <div className="col text-right">
                                        <button className=" btn btn-danger btn-lg" onClick={this.continue}>
                                            Next 
                                        </button>
                                    </div>
                                    
                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                </div>
            </div>
            </>
        )
    }
}

class Add2 extends React.Component {
    back = e => {
        e.preventDefault();
        this.props.prevStep();
    }
  
    submit = e =>{
        e.preventDefault();
        this.props.submitHandler(e);
    }
    
    render(){
        // const {  } = this.props;
        return(
            <>
             <div className="container">
                {this.props.insert_id}
                    <div className="row">
                        <div className="col-lg-3 col-md-3 hidden-sm"></div>
                          <div className="col-lg-6 col-md-6 hidden-sm">
                              <div className="card" style={{paddingTop: '2%'}}>
                                  <div className="card-body"> 
                                      <form onSubmit={this.submit} encType="multipart/form-data">
                                        <h6><b>Images of dress<span className="required-star">*</span></b></h6><br />
                                            <mark>Plzz take 3 images of same dress</mark>
                                          <input type="file"  name="file" class="form-control"  onChange={this.props.onFileChange} multiple/><br />
                                          <div className="col text-left">
                                              <button className="btn btn-danger btn-lg" onClick={this.back}>
                                                   Back
                                              </button>
                                          </div>
                                          <div className="col text-right">
                                              <input type="submit" className=" btn btn-success btn-lg"  value="Submit "/>
                                              {/* <button type="submit" className="btn btn-success" >
                                                  Submit 
                                              </button> */}
                                          </div>
                                      </form>
                                  </div>
                              </div>
                          </div>
                        <div className="col-lg-2 col-md-2 hidden-sm"></div>
                    </div>
                </div>
            </>
        );
    }
}

