import React from 'react';
import MainNav from './MainNav';
import Footer from './Footer';
import axios from 'axios';

class Contact extends React.Component{
    constructor(props){
        super(props)
        this.state={
            name:'',
            email:'',
            phone:'',
            query:'',
            dataStatus:false,
            dataState:false,
        }
    }
    handleChange = (e) =>{
        e.preventDefault();
        this.setState({
            [e.target.name] : e.target.value
        })
    }
    submitHandler=(e) => {
        e.preventDefault();
        axios.post('http://localhost:4000/customer/contact',this.state)
        .then(response => {
            if(response.data.status === 1){
                this.setState({
                    dataState: true,
                    name:'',
                    email:'',
                    phone:'',
                    query:'',
                })
            }else{
                this.setState({
                    dataStatus:true,
                    name:'',
                    email:'',
                    phone:'',
                    query:'',
                })
                alert("not submitted");
            }
            setInterval(() => {
                this.setState({dataState : false})
            },10000)
        })
    }
    render(){
        const{ email,name,query,phone,dataStatus,dataState }=this.state
        return(
            <>
               <MainNav /><br /><br /><br />
               <div className="container" style={{padding:'3px'}}>
                   <div className="row">
                       <div className="col-lg-3 col-md-3 hidden-sm"></div>
                       <div className="col-lg-6 col-md-6 hidden-sm">
                           <div className="card" style={{padding: '4%'}}>
                               <h6 style={{color:'red'}}><b>Contact US</b></h6><hr />
                               { dataState ? <div className="alert alert-success">Successfully updated</div>:null}
                               { dataStatus ? <div className="alert alert-danger">Failed to updated</div>:null}
                               <form onSubmit={this.submitHandler}>
                                    <h6><b>Name</b></h6>
                                        <input type="text" className="form-control" placeholder="Enter your name" name="name" onChange={this.handleChange} value={name} required="TRUE"/><br />           
                                    <h6><b>Phone</b></h6>
                                        <input type="number" className="form-control" placeholder="Enter your phone" name="phone" onChange={this.handleChange} value={phone} required="TRUE"/><br />
                                    <h6><b>Email</b></h6>
                                        <input type="email" className="form-control" placeholder="Enter your email" name="email" onChange={this.handleChange} value={email} required="TRUE"/><br />           
                                    <h6><b>Report your Query</b></h6>
                                        <textarea type="msg" className="form-control" placeholder="Enter your query" name="query" onChange={this.handleChange} value={query} required="TRUE"/><br />
                                        <input type="submit" className="btn btn-success btn-lg" value="Submit" />
                                </form>
                           </div>
                       </div>
                       <div className="col-lg-3 col-md-3 hidden-sm"></div>
                   </div>
               </div>
               <div>
                   <Footer />
               </div>
            </>
        )
    }
}

export default Contact;