import React from 'react';
import { Component } from 'react';
import {Link} from 'react-router-dom';
// import Styles from '../Styles/Header.css'


class Nav extends Component{
    render(){
        return(
            <div className="HeaderClass">
            <nav className="navbar fixed-top navbar-expand-md navbar-light bg-light">
                <Link className="navbar-brand" to="/sdashboard"><h4>Home</h4></Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse " id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                            <ul className="nav-link btn btn-success btn-sm" style={{ color: 'white' }}>Active</ul>
                        </li>
                        <li className="nav-item dropdown">
                            <Link className="nav-link dropdown-toggle" to="" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <i className="fa fa-cog" aria-hidden="true"></i>
                            </Link>
                            <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                            <Link className="dropdown-item" to="/update">update Profile</Link>
                            <Link className="dropdown-item" to="/logout" >Logout<i className="fa fa-sign-out" aria-hidden="true"></i></Link>
                            <div className="dropdown-divider"></div>
                            <Link className="dropdown-item" to="">Something else here</Link>
                            </div>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link"  to="/products">Add Products</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/s_orders">Orders <i className="fas fa-tshirt" aria-hidden="true"></i></Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/s_orderhistory">Order History <i className="fa fa-history" aria-hidden="true"></i></Link>
                        </li>
                    </ul>
                    
                </div>
            </nav>    
        </div>
        );
    }
}


export default Nav;