import React from 'react';
import CNav from './CNav';
import Footer from './Footer';
import '../Assets/Css/comment.css';
import axios from 'axios';
import { Redirect } from 'react-router-dom';
// import nodatafound from '../Assets/Images/nodatafound.png';
import loading from '../Assets/Images/spinner-loader-animation.gif';


class OrderHistory extends React.Component{
    constructor(props){
        super(props)

        const token = localStorage.getItem("token")
        let Data = JSON.parse(token)

        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            cid:Data[0],
            loggedIn,
            Details:[],
            loading: false,
            

        }
    }

    componentDidMount(){
        axios.post("http://localhost:4000/customer/orderData",this.state)
            .then(response => {
                // console.log(response)
                if(response.data.status === 1){
                        this.setState({
                            Details:response.data.result,
                        })
                }else{
                    alert("No Data Found");
                }
            })
    }

    
    render(){
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
            const{ Details,loading }=this.state
        return(
            <>
               <CNav /><br /><br /><br />
               <div>
               <div className="container-fluid">
                    {/* <Welcome /><br /> */}
                    <div className="row">
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                        {/* {dataStatus ? <Loading /> : */}
                        <div className="col-lg-10 col-md-10 hidden-sm">
                        {loading ? <Loading /> :
                            <div className="card">
                                <div className="card-body">
                                    <div className="table-responsive">
                                        <table className="table  table-hover" border="2px">
                                            <thead className="thead-dark">
                                                <div>
                                                <tr>
                                                    <th>#</th>
                                                    <th>Image</th>
                                                    {/* <th>Customer id</th>
                                                    <th>Item id</th> */}
                                                    <th>Item Colour</th>
                                                    <th>Item Size</th>
                                                    <th>Price</th>
                                                    <th>Shop name</th>
                                                    {/*  */}
                                                    <th>Shop phonenumber</th>
                                                    <th>H-no</th>
                                                    <th>Area</th>
                                                    {/* <th>City</th> */}
                                                    <th>Payment GateWay</th>
                                                    <th>Delivery Boy</th>
                                                    
                                                </tr>
                                                </div>
                                            </thead>
                                            <tbody className="tbody-light">
                                    
                                            { Details.map((data) => (
                                                    <tr key={data}>
                                                        {`${data.status}`>='3' ? <div>
                                                        <td>{data.order_id}</td>
                                                        <td><a target="_blank" href={'http://localhost:4000/images/'+data.item_image}><img src={'http://localhost:4000/images/'+data.item_image} alt={data.image1} height="70px" width="70px" /></a></td>
                                                        {/* <td>{data.customer_id}</td>
                                                        <td>{data.item_id}</td> */}
                                                        <td>{data.item_colour}</td>
                                                        <td>{data.item_size}</td>
                                                        <td>{data.item_price}</td>
                                                        <td>{data.shop}</td>
                                                        {/* <td>{data.c_email}</td> */}
                                                        <td>{data.s_phone}</td>
                                                        <td>{data.hno}</td>
                                                        <td>{data.area}</td>
                                                        {/* <td>{data.city}</td> */}
                                                        <td>{data.payment}</td>
                                                        <td>{data.delivery_boy}</td>
                                                    
                                                        </div>:null}
                                                        
                                                    </tr>
                                            ))}  
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        }
                        </div>
                    {/* } */}
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                    </div>
                </div>b
               </div>
               <div>
                   <Footer />
               </div>
            </>
        )
    }
}
}
class Loading extends React.Component{
    render(){
        return(
            <div className="loading">
                <center><img src={loading} alt="loading" width="400px" height="400px"/></center>
            </div>
        )
    }
}

