import React from 'react';
import CNav from './CNav';
import { Redirect,Link } from 'react-router-dom';
import axios from 'axios';
import Footer from './Footer';
// import update from '../Assets/Css/update.css';

class C_updateDetails extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        let rData = JSON.parse(token)

        let loggedIn = true
        if(token == null){
            loggedIn = false
        }
        this.state = {
            rData,
            id:rData[0],
            loggedIn,
            data:'',

        }
    }

    componentDidMount() {
        // console.log(this.state.id)
        axios.post("http://localhost:4000/customer/Details",{id:this.state.id})
            .then(response => {
                // console.log(response.data.result[0])
                this.setState({data : response.data.result[0]
                })
                
            })
            .catch(err => {
                console.log(err);
            })
        
    }

    render(){
        if(this.state.loggedIn === false){
            return <Redirect to="/" />
        }
        const{ data } = this.state
        return(
            
            <div className="hi">
                <CNav /><br /><br /><br />
                <div className="container" style={{paddingTop: '5%'}}>
                  <div className="row">
                      <div className="col-lg-3 col-md-3 hidden-sm"></div>
                        <div className="col-lg-6 col-md-6 hidden-sm">
                            <div align="center">
                            <   div className="card details" style={{paddingTop: '2%'}}>
                            {/* {
                                data.map(res => ( */}
                                    <div className="card-body table-responsive"> 
                                        <table>
                                            <tbody>
                                                <tr>
                                                    <td><h5><b><em> Name</em></b></h5></td> : 
                                                    <td><h5>{data.name}</h5></td>
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>Email</em></b></h5></td> :
                                                    <td><h5>{data.email}</h5></td>
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>PhoneNumber</em></b></h5></td> :
                                                    <td><h5>{data.phone}</h5></td>
                                                    
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>House no.</em></b></h5></td> :
                                                    <td><h5>{data.Hno}</h5></td>
                                                    
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>Area</em></b></h5></td> :  
                                                    <td><h5>{data.area}</h5></td>
                                                    
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>City</em></b></h5></td> :
                                                    <td><h5>{data.city}</h5></td>
                                                    
                                                </tr>
                                                <tr>
                                                    <td><h5><b><em>Pincode</em></b></h5></td> :
                                                    <td><h6>{data.pincode}</h6></td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div><br />
                                    <h5><em>Update the Adreess data if U are not Updated*</em></h5><br />
                                    <div align="center">
                                        <Link className="btn btn-danger btn-lg" to="/c_update">Update</Link>
                                    </div>
                                </div>
                                {/* ))
                            } */}
                            </div>
                        </div>
                      <div className="col-lg-2 col-md-2 hidden-sm"></div>
                  </div>
              </div>
               <div>
                   <Footer />
               </div>
            </div>
        )
    }
}

export default C_updateDetails;