import React from 'react';
import { Redirect } from 'react-router-dom';
import axios from 'axios'; 
import CNav from './CNav';
import Footer from './Footer';

class C_update extends React.Component{
    constructor(props){
        super(props)

        const token = localStorage.getItem("token")
        let rData = JSON.parse(token)

        let loggedIn = true
        if(token == null){
            loggedIn = false
        }
        this.state = {
            loggedIn,
            rData,
            id:rData[0],
            hno:'',
            area:'',
            city:'',
            pincode:'',
            question:'',
            dataStatus:false,
            dataState:false,
        }
    }

    handleChange =(e) => {
        e.preventDefault();
        this.setState({
            [e.target.name] : e.target.value
        })
    }

    submitHandler = (e) => {
        e.preventDefault();
        axios.post("http://localhost:4000/customer/update",this.state)
            .then(res => {
                console.log(res)
                if(res.data.status === 1){
                    this.setState({
                        dataStatus: true,
                    })
                }else{
                    this.setState({
                        dataState: true,
                        hno:'',
                        area:'',
                        city:'',
                        pincode:'',
                        question:'',
                    })
                }
                setInterval(() => {
                    this.setState({
                        dataState : false,
                        dataStatus: false
                    })
                },10000)
            })
    }

    render(){
        if(this.state.loggedIn === false){
            return <Redirect to="/" />
        }
        const{ hno,area,city,pincode,dataState,dataStatus, }=this.state
        return(
            <>
                <CNav /><br /><br /><br />
                <div className="container" style={{paddingTop: '5%'}}>
                  <div className="row">
                      <div className="col-lg-3 col-md-3 hidden-sm"></div>
                        <div className="col-lg-6 col-md-6 hidden-sm">
                            { dataState ? <div className="alert alert-danger">Not Submitted</div> : null }
                            { dataStatus ? <div className="alert alert-success">Successfully Submitted</div> : null }
                            <div className="card" style={{paddingTop: '2%'}}>
                                <div className="card-body"> 
                                    <h5><em>U can re-update if u have to change the Address</em></h5><br></br>
                                    <form onSubmit={this.submitHandler}>
                                        <h6><em>House Number*</em></h6><br />
                                            <input type="text" className="form-control" placeholder="Enter your Houseno." name="hno" onChange={this.handleChange} value={hno} required="TRUE"/><br />
                                        <h6><em>Area*</em></h6><br />
                                            <textarea type="msg" className="form-control" placeholder="Enter your Area" name="area" onChange={this.handleChange} value={area} required="TRUE"/><br />
                                        <h6><em>City*</em></h6><br />
                                            <input type="text" className="form-control" placeholder="Enter your city" name="city" onChange={this.handleChange} value={city} required="TRUE"/><br />
                                        <h6><em>Pincode*</em></h6><br />
                                            <input type="number" className="form-control" placeholder="Enter your pincode" name="pincode" onChange={this.handleChange} value={pincode} required="TRUE"/><br />
                                        <h6>Enter any Crazy Answer is used in change passowrd</h6>
                                        <h6>What do u like most?</h6>
                                        <select className="form-control" name="question" onChange={this.handleChange} required="TRUE">
                                            <option>--Select the Category--</option>
                                            <option value="food">Food</option>
                                            <option value="love">Lover/wife</option>
                                            <option value="you">Yourself</option>
                                            <option value="none">none</option>
                                        </select><br />
                                        <div align="center">
                                            <button type="submit" className="btn btn-danger btn-lg">Update</button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                      <div className="col-lg-2 col-md-2 hidden-sm"></div>
                  </div>
              </div>
                <div>
                   <Footer />
               </div>
            </>
        )
    }
}

export default C_update;