import React from 'react';
import CNav from './CNav';
import axios from 'axios';
import {Redirect} from 'react-router-dom';
import welcome from '../Assets/Images/welcome.gif';
import nodatafound from '../Assets/Images/nodatafound.png';
import Footer from './Footer';

class C_dashboard extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        let Data = JSON.parse(token)

        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            cid:Data[0],
            c_updatestatus:Data[1],
            loc:'',
            loggedIn,
            dataState : false,
            dataStatus:false,
            data:[],
        }
    }

    handleChange = (e) => {
        e.preventDefault();
        this.setState({
            [e.target.name] : e.target.value,
            data: [],
            dataState : false
        })
        axios.post('http://localhost:4000/customer/search',this.state)
            .then(res => {
                // console.log(res.data);
                this.setState({
                    data : res.data,
                })
                if(res.data.length > 0){
                    this.setState({dataState: true})
                }else{
                    this.setState({ dataStatus: true})
                }
                
            })
    }


    Search =  (e) => {
        alert(this.state.loc);
    }

    render(){
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
            const { loc,dataState,data,dataStatus }=this.state;
        return(
            <>
               <CNav /><br /><br /><br />
               { `${this.state.c_updatestatus}` === "0" ? 
               <div>
                   <div className="alert alert-danger">plzz update your profile and login once again place the orders</div>
                   <div>
                        <center><Welcome /></center>
                   </div>
                </div> : null }
                { `${this.state.c_updatestatus}` === "1" ? 
               <div>
                   <div>
                        <center><Welcome /></center><br />
                        <div className="container">
                            <div className="row">
                            <div className="col-lg-4 col-md-4 hidden-sm"></div>
                            <div className="col-lg-4 col-md-4 hidden-sm">
                                <div align="center">
                                    <form onSubmit={this.Search} autoComplete="off">
                                        <h6><b><em>Enter the place or Location u want buy the products/Dresses *</em></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter your Location" name="loc" value={loc} onChange={this.handleChange} required="TRUE" /><br />
                                        {dataState ? <div id="searchData">
                                            <ul className="list-group">
                                                {
                                                    data.map((items) =>(
                                                        <a href={"/shops/"+items.location} key={items.id} ><li className="list-group-item searchIndivi"><div className="">{items.location}  </div></li></a> 
                                                    ))
                                                }
                                            </ul>
                                        </div> : null}
                                        {dataStatus ? <NoData /> : null}
                                        {/* <button type="submit" className="btn btn-success btn-lg">Submit</button> */}
                                    </form>
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-4 hidden-sm"></div>
                            </div>
                        </div>
                   </div>
                </div> : null }
               <div>
                   <Footer />
               </div>
            </>
        )
    }
}
}

class Welcome extends React.Component{
    render(){
        return(
            <div className="welcome">
                <center><img src={welcome} alt="welcome"  width="400px" height="100px" /></center>
            </div>
        )
    }
}

class NoData extends React.Component{
    render(){
        return(
            <div className="welcome">
                <center><img src={nodatafound} alt="nodatafound"  width="400px" height="400px" /></center>
            </div>
        )
    }
}

export default C_dashboard;