import React from 'react';
import MainNav from './MainNav';
import Footer from './Footer';
import axios from 'axios';

class C_register extends React.Component{
    constructor(props){
        super(props)
        this.state={
            name:'',
            phone:'',
            gender:'',
            email:'',
            pwd:'',
            c_pwd:'',
            dataState:false

        }
    }

    handleChange = (e) => {
        this.setState({[e.target.name]: e.target.value});
    }

    submitHandler=(e) =>{
        e.preventDefault();
        axios.post("http://localhost:4000/customer/register",this.state)
        .then(response => {
            if(response.status === 200){
              this.setState({
                 dataState: true,
                    name:'',
                    phone:'',
                    gender:'',
                    email:'',
                    pwd:'',
                    c_pwd:'',

              })
            }else{
              alert("Not submitted..!");
            }
        })
        .catch(err => {
            console.log(err)
        })
        setInterval(() => {
            this.setState({dataState : false,
            })
        },10000)
    }
    render(){
        const{ dataState,name,phone,email,pwd,c_pwd }=this.state
        return(
            <>
               <MainNav /><br /><br /><br />
               <div className="container">
                <div className="row">
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                    <div className="col-lg-6 col-md-6 hidden-sm">
                        <div className="card">
                            { dataState ? <div className="alert alert-success">Successfully submited</div> : null}
                            <div align="center">
                                <h3><b>Customer Registration</b></h3><hr />
                            </div>
                            <div className="card-body">
                                <form onSubmit={this.submitHandler}>
                                    <h6><b>Name<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the name" name="name" value={name} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Gender<span className="required-star">*</span></b></h6>
                                    <select className="form-control" name="gender" onChange={this.handleChange} required="TRUE">
                                        <option>--Select the Category--</option>
                                        <option value="Male">Male</option>
                                        <option value="Female">Female</option>
                                        <option value="Others">Others</option>
                                    </select><br />
                                    <h6><b>Phone Number<span className="required-star">*</span></b></h6>
                                        <input type="number" className="form-control" placeholder="Enter the phone number" name="phone" value={phone} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Email<span className="required-star">*</span></b></h6>
                                        <input type="email" className="form-control" placeholder="Enter the Email" name="email" value={email} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Password<span className="required-star">*</span></b></h6>
                                        <input type="password" className="form-control" placeholder="Enter the password" name="pwd" value={pwd} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Confirm_Password<span className="required-star">*</span></b></h6>
                                        <input type="password" className="form-control" placeholder="Enter the password" name="c_pwd" value={c_pwd} onChange={this.handleChange} required="TRUE"/><br />
                                        <button type="submit" className=" btn btn-danger btn-lg">
                                            Submit 
                                        </button>
                                    
                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                </div>
            </div>
 
               <div>
                   <Footer />
               </div>
            </>
        )
    }
}

export default C_register;