import React from 'react';
import CNav from './CNav';
import Footer from './Footer';
import { Redirect,Link } from 'react-router-dom';
import axios from 'axios';
import nodatafound from '../Assets/Images/nodatafound.png';


class Item1 extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        // let Data = JSON.parse(token)

        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            loggedIn,
            cat: props.match.params.cat,
            shop: props.match.params.shop,
            search:'',
            data:[],
            Data:[],
            dataState : false,
            dataStatus:false,
        }
    }

    componentDidMount(){
        axios.post('http://localhost:4000/customer/item1',this.state)
        .then(res => {
            // console.log(res);
            if(res.data.status === 1){
            this.setState({
                // dataStatus: false,
                Data: res.data.result, 
            })
        }else{
            this.setState({
                // dataStatus:false,
            })
            alert("No data Found");
        }
        })
    }

    handleChange = (e) => {
        e.preventDefault();
        this.setState({
            [e.target.name] : e.target.value,
            data: [],
            dataState : false
        })
        axios.post('http://localhost:4000/customer/search2',this.state)
            .then(res => {
                // console.log(res.data);
                this.setState({
                    data : res.data,
                })
                if(res.data.length > 0){
                    this.setState({dataState: true})
                }else{
                    this.setState({ dataStatus: true})
                }
                
            })
    }

    Search =  (e) => {
        alert(this.state.search);
    }


    render(){
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
            const { search,dataState,data,dataStatus,Data }=this.state;
        return(
            <>
               <CNav /><br /><br /><br />
                    <div className="container">
                        <div className="row">
                        <div className="col-lg-4 col-md-4 hidden-sm"></div>
                        <div className="col-lg-4 col-md-4 hidden-sm">
                            <div align="center">
                                <form onSubmit={this.Search} autoComplete="off">
                                    <h6><b><em>Search the item u want like shirts,pants.etc *</em></b></h6>
                                    <input type="text" className="form-control" placeholder="Enter your Item" name="search" value={search} onChange={this.handleChange}  /><br />
                                    {dataState ? <div id="searchData">
                                        <ul className="list-group">
                                            {
                                                data.map((items) =>(
                                                    <a href={`/item2/${items.category}/${items.shop_name}/${items.item}`} key={items.id} ><li className="list-group-item searchIndivi"><div className="" >{items.item} </div></li></a> 
                                                ))
                                            }
                                        </ul>
                                    </div> : null}
                                    {dataStatus ? <NoData /> : null}
                                    {/* <button type="submit" className="btn btn-success btn-lg">Submit</button> */}
                                </form>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-4 hidden-sm"></div>
                        </div>
                    </div>
                    <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-md-2 hidden-sm"></div>
                       {/* <div className="col-lg-2 col-md-2 hidden-sm"></div> */}
                       {/* <div className="col-lg-8 col-md-8 hidden-sm"> */}
                           {
                               Data.map((data) => (
                                   <div className="col-md-4 col-12">
                                        <div className="card text-black bg-white border-0" key={data.id} style={{width: '18rem',height: '28rem'}}>
                                            <div className="Image" style={{padding: '3%'}}>
                                                <img src={'http://localhost:4000/images/'+data.image1} className="card-image-top" alt={data.image} height="180px" width="100%" />
                                            </div>
                                            <div className="card-body text-dark">
                                                <center><h2 className="card-title" style={{color: 'red'}}><b><em>{ data.brand }</em></b></h2></center>
                                                <p style={{fontSize:'22px'}}><s>Rs.{ data.dprice}</s></p><span><p  style={{fontSize:'22px'}}>Rs.{ data.price}</p></span>
                                                    <Link type="button" to={`/itemsData/${data.id}/${data.shop_name}`} className="btn btn-dark btn-lg">View Item</Link>
                                            </div>
                                        </div><br />
                                    </div>
                               ))
                           }
                       {/* </div> */}
                       {/* <div className="col-lg-2 col-md-2 hidden-sm"></div> */}
                   </div>
                    </div>
                    <div>
                        <Footer />
                    </div>
            </>
        )
    }
}
}

class NoData extends React.Component{
    render(){
        return(
            <div className="welcome">
                <center><img src={nodatafound} alt="nodatafound"  width="400px" height="400px" /></center>
            </div>
        )
    }
}

export default Item1;