import React from 'react';
import ANav from './ANav';
import Footer from './Footer';
import { Redirect } from 'react-router-dom';
// import loading from '../Assets/Images/spinner-loader-animation.gif';
import axios from 'axios';

class ApprovedShops extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            loggedIn,
            Data:[],
            dataState: false,
            notApproved: false,
            // dataStatus:true,
            // loading:true,

        }
    }

    componentDidMount(){
        axios.get('http://localhost:4000/login/AproovedDetails')
        .then(res => {
            // console.log(res);
            if(res.status === 200){
            this.setState({
                // dataStatus: false,
                Data: res.data, 
            })
        }else{
            this.setState({
                // dataStatus:false,
            })
            alert("No data Found");
        }
        })
    }

    unapprove=(e) =>{
        e.preventDefault();
        // this.setState({ loading: true })
        axios.post("http://localhost:4000/login/unapprove",{id:+e.target.value})
            .then(response => {
                // console.log(response)
                if(response.data.status === 1){
                    this.setState({
                        dataState:true
                    })
                    window.location.reload(true)
                }else{
                    this.setState({
                        notApproved: true,
                    })
                    this.props.history.push('/a_dashboard')
                }
                
            })
            .catch(err => {
                console.log(err)
            })
            setInterval(() => {
                this.setState({dataState : false,
                    notApproved:false
                })
            },10000)
    }

    render(){
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
            const {
                Data,dataState,notApproved
            }= this.state
        return(
            <>
                <ANav /><br /><br /><br />
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                        {/* {dataStatus ? <Loading /> : */}
                        <div className="col-lg-10 col-md-10 hidden-sm"><br />
                        {/* {loading ? <Loading /> : */}
                            <div className="card">
                                { dataState ? <div className="alert alert-success">Successfully Updated</div> : null}
                                <div className="card-body">
                                    { notApproved ? <div className="alert alert-danger">Not Updated</div>: null}
                                    <div className="table-responsive">
                                        <table className="table  table-hover" border="2px">
                                            <thead className="thead-dark">
                                                <tr>
                                                    <th>#</th>
                                                    <th>Image</th>
                                                    <th>Shop Name</th>
                                                    <th>Owner Name</th>
                                                    <th>Email</th>
                                                    <th>Location</th>
                                                    <th>Category</th>
                                                    <th>Phone Number</th>
                                                    <th>Description</th>
                                                    <th>Aproove</th>
                                                </tr>
                                            </thead>
                                            <tbody className="tbody-light">
                                            { Data.map((data) => (
                                                    <tr key={data}>
                                                        <td>{data.id}</td>
                                                        <td><img src={'http://localhost:4000/images/'+data.image} alt={data.image} height="100px" width="100px" /></td>
                                                        <td>{data.shopname}</td>
                                                        <td>{data.owner}</td>
                                                        <td>{data.email}</td>
                                                        <td>{data.location}</td>
                                                        <td>{data.cat1},{data.cat2},{data.cat3},</td>
                                                        <td>{data.phone}</td>
                                                        <td>{data.description}</td>
                                                        <td><button type="button" class="btn btn-danger" value={data.id}  onClick={this.unapprove} >Un Approve</button></td>   
                                                    </tr>
                                            ))}  
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                         {/* }  */}
                        </div>
                    {/* } */}
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                    </div>
                </div>
                <div>
                   <Footer />
                </div>
            </>
        )
    }
}
}

