import React from 'react';
import MainNav from './MainNav';
import Footer from './Footer';
import one from '../Assets/Images/1.jpg';
import two from '../Assets/Images/2.jpg';
import three from '../Assets/Images/3.jpg';
import five from '../Assets/Images/5.png';
import four from '../Assets/Images/4.jpg';
import six from '../Assets/Images/6.jpg';
// import '../Assets/Css/start.css';

class Home extends React.Component{
    render(){
        return(
            <>
               <MainNav /><br /><br />
               <div className="container-fluid">
                   <div className="row">
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                        <div className="col-lg-10 col-md-10 hidden-sm">
                            <div className="alert alert-warning" style={{height:'70px',fontSize:'22px',paddingTop:'2%'}}><center>Plzz Register and Login the Account for shops or Shopping</center></div>
                                <div id="demo" className="carousel slide" data-ride="carousel" style={{ }}>
                                    <ul className="carousel-indicators">
                                    <li data-target="#demo" data-slide-to="0" class="active"></li>
                                    <li data-target="#demo" data-slide-to="1"></li>
                                    <li data-target="#demo" data-slide-to="2"></li>
                                    </ul>

                                    
                                    <div className="carousel-inner">
                                        <div className="carousel-item active">
                                        
                                            <img src={one}  alt={one} width="1500px" height="500px" />
                                        
                                        </div>
                                        <div className="carousel-item">
                                        
                                            <img src={two}  alt={two} width="1500px" height="500px" />
                                        
                                        </div>
                                        <div className="carousel-item">
                                        
                                            <img src={three}  alt={three} width="1500px" height="500px" />
                                        
                                        </div>
                                    </div>

                                    <a className="carousel-control-prev" href="#demo" data-slide="prev">
                                    <span className="carousel-control-prev-icon"></span>
                                    </a>
                                    <a className="carousel-control-next" href="#demo" data-slide="next">
                                    <span className="carousel-control-next-icon"></span>
                                    </a>
                                </div><br />
                        </div>
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                        <div className="col-lg-10 col-md-10 hidden-sm">
                            <div className="jumbotron">
                                <div className="container">
                                    <div className="row">
                                        <div  className="col-lg-3 col-md-3 hidden-sm">
                                            <div align="center">
                                                <img src={five} alt={five} width="200px" height="200px"/>
                                            </div>
                                        </div>
                                        <div  className="col-lg-8 col-md-8 hidden-sm">
                                            <center><p style={{fontSize:'25px',color:'orange'}}><b>Whoever said that money can't buy happiness simply didn't know where to go shopping. ...</b></p></center>
                                            <center><p style={{fontSize:'25px',color:'blue'}}><b>I always say shopping is cheaper than a psychiatrist. ...</b></p></center>      
                                            <center><p style={{fontSize:'25px',color:'green'}}><b>Happiness is not in money, but in shopping. ...</b></p></center>      
                                        </div>
                                        <div  className="col-lg-1 col-md-1 hidden-sm"></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                        <div className="col-lg-10 col-md-10 hidden-sm">
                            <img src={four} alt={four} width="100%" height="50%" />
                            <img src={six} alt={six} width="100%" height="50%" />
                        </div>
                        <div className="col-lg-1 col-md-1 hidden-sm"></div>
                    </div>
                </div>  
               
               <div>
                   <Footer />
               </div>
            </>
        )
    }
}

export default Home;