import React from 'react';
import MainNav from './MainNav';
import Footer from './Footer';
import axios from 'axios';

class SF_pwd extends React.Component{
    constructor(props){
        super(props)
        this.state={
            question:'',
            email:'',
            password:'',
            dataStatus:false,
            dataState:false,
        }
    }
    handleChange = (e) =>{
        e.preventDefault();
        this.setState({
            [e.target.name] : e.target.value
        })
    }
    submitHandler=(e) => {
        e.preventDefault();
        axios.post('http://localhost:4000/customer/s_f_pwd',this.state)
        .then(response => {
            if(response.data.status === 1){
                this.setState({
                    dataState: true,
                    question:'',
                    email:'',
                    password:'',
                })
            }else{
                this.setState({
                    dataStatus:true,
                    question:'',
                    email:'',
                    password:'',
                })
                alert("not submitted");
            }
            setInterval(() => {
                this.setState({dataState : false})
            },10000)
        })
    }
    render(){
        const{ email,password,dataStatus,dataState }=this.state
        return(
            <>
               <MainNav /><br /><br /><br />
               <div className="container">
                   <div className="row">
                       <div className="col-lg-3 col-md-3 hidden-sm"></div>
                       <div className="col-lg-6 col-md-6 hidden-sm">
                           <div className="card" style={{padding: '4%'}}>
                               { dataState ? <div className="alert alert-success">Successfully updated</div>:null}
                               { dataStatus ? <div className="alert alert-danger">Failed to updated</div>:null}
                               <form onSubmit={this.submitHandler}>
                                    <h6><b>Enter same Answer that answered on registeration</b></h6>
                                        <h6><b>What do u like most?</b></h6>
                                        <select className="form-control" name="question" onChange={this.handleChange} required="TRUE">
                                            <option>--Select the Category--</option>
                                            <option value="food">Food</option>
                                            <option value="love">Lover/wife</option>
                                            <option value="you">Yourself</option>
                                            <option value="none">none</option>
                                        </select><br />
                                    <h6><b>Email</b></h6>
                                        <input type="email" className="form-control" placeholder="Enter your email" name="email" onChange={this.handleChange} value={email} required="TRUE"/><br />
                                    <h6><b>Enter New Password</b></h6>
                                        <input type="password" className="form-control" placeholder="Enter your password" name="password" onChange={this.handleChange} value={password} required="TRUE"/><br />
                                        <input type="submit" className="btn btn-success btn-lg" value="Submit" />
                                </form>
                           </div>
                       </div>
                       <div className="col-lg-3 col-md-3 hidden-sm"></div>
                   </div>
               </div>
               <div>
                   <Footer />
               </div>
            </>
        )
    }
}

export default SF_pwd;